<template>
  <div id="vitabox_list">
    <v-container fluid>
       <v-select label="Search" autocomplete cache-items :items="vitaboxes" item-text="address" :search-input.sync="search" v-model="select"></v-select>
    </v-container>
    <v-card id="select-card">
      <v-layout row wrap v-if="select">
        <v-flex xs12 sm9 md8>
          <table>
            <tr>
              <th class="text-xs-right"><label class="subheading primary_d--text mx-2">id</label></th>
              <th><h3>{{select.id}}</h3></th>
            </tr>
              <tr>
              <th class="text-xs-right"><label class="subheading primary_d--text mx-2">registered</label></th>
              <th>
                <h3 v-if="select.registered"><v-icon>check</v-icon></h3>
                <h3 v-else><v-icon>close</v-icon></h3>
              </th>
            </tr>
            <tr>
              <th class="text-xs-right"><label class="subheading primary_d--text mx-2">active</label></th>
              <th>
                <h3 v-if="select.active"><v-icon>check</v-icon></h3>
                <h3 v-else><v-icon>close</v-icon></h3>
              </th>
            </tr>
            <tr>
              <th class="text-xs-right"><label class="subheading primary_d--text mx-2">since</label></th>
              <th><h3>{{select.created_at}}</h3></th>
            </tr>
            <tr>
              <th class="text-xs-right"><label class="subheading primary_d--text mx-2">last alteration</label></th>
              <th><h3>{{select.updated_at}}</h3></th>
            </tr>
          </table>
        </v-flex>
        <v-flex xs12 sm3 md4>
          <v-layout row wrap class=" text-xs-right">
            <v-flex xs4 sm12>
              <v-bottom-sheet v-model="settings_sheet" inset lazy>
                <v-btn slot="activator" color="pink" dark><v-icon>settings</v-icon></v-btn>
                <vitabox-settings :settings="select.settings" @close="settings_sheet=false"></vitabox-settings>
              </v-bottom-sheet>
            </v-flex>
            <v-flex xs4 sm12>
              <v-bottom-sheet v-model="patients_sheet" inset lazy>
                <v-btn slot="activator" color="purple" dark><v-icon>group</v-icon></v-btn>
                <vitabox-patients :item="select" @close="patients_sheet=false"></vitabox-patients>
              </v-bottom-sheet>
            </v-flex>
            <v-flex xs4 sm12>
              <v-bottom-sheet v-model="boards_sheet" inset lazy>
                <v-btn slot="activator" color="indigo" dark><v-icon>settings_remote</v-icon></v-btn>
                <vitabox-boards :item="select" @close="boards_sheet=false"></vitabox-boards>
              </v-bottom-sheet>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-layout>
      <br>
      <div id="google-map-list"></div>
    </v-card>

  </div>
</template>

<script>
import { event_bus } from "@/plugins/bus.js";
import VitaboxPatients from "./Patients";
import VitaboxBoards from "./Boards";
import VitaboxSettings from "./Settings";

export default {
  name: "vitabox_list",
  data: () => {
    return {
      search: "",
      headers: [
        {
          text: "Address",
          align: "left",
          value: "address"
        }
      ],
      select: null,
      vitaboxes: [],
      map: null,
      marker: null,
      patients_sheet: false,
      settings_sheet: false,
      boards_sheet: false
    };
  },
  created() {
    event_bus.$emit("waiting", 50);
    event_bus.$data.http
      .get("/vitabox")
      .then(response => {
        this.vitaboxes = response.data.vitaboxes;
        event_bus.$emit("waiting", 50);
      })
      .catch(error => {
        if (error.response) {
          event_bus.$emit("toast", {
            message: error.response.data,
            type: "error"
          });
        } else {
          event_bus.$emit("toast", {
            message: error.message,
            type: "error"
          });
        }
        event_bus.$emit("waiting", 50);
      });
  },
  watch: {
    select(val) {
      if (val.latitude && val.longitude) {
        const element = document.getElementById("google-map-list");
        var myLatLng = new google.maps.LatLng(val.latitude, val.longitude);
        const options = {
          zoom: 15,
          center: myLatLng
        };
        this.map = new google.maps.Map(element, options);
        this.marker = new google.maps.Marker({
          position: myLatLng,
          map: this.map,
          title: val.address
        });
      }
      let monthNames = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec"
      ];
      let d = new Date(val.created_at);
      this.select.created_at =
        d.getDate() + " " + monthNames[d.getMonth()] + " " + d.getFullYear();
      d = new Date(val.updated_at);
      this.select.updated_at =
        d.getDate() + " " + monthNames[d.getMonth()] + " " + d.getFullYear();
    }
  },
  components: {
    "vitabox-patients": VitaboxPatients,
    "vitabox-settings": VitaboxSettings,
    "vitabox-boards": VitaboxBoards
  }
};
</script>

<style>
th h3 {
  text-align: left;
}
#select-card {
  background-color: transparent;
}
#google-map-list {
  width: 100%;
  height: 250px;
}
</style>


