<template>
  <v-list id="account_menu">
    <router-link v-for='(link,index) in links.account' :key='link.name + index' :to='link.path'>
      <v-list-tile class="account_options account_notchoosen ash--text">
        <v-list-tile-action>
          <v-icon v-if="link.icon">{{link.icon}}</v-icon>
        </v-list-tile-action>
        <v-list-tile-content>
          <v-list-tile-title v-text="link.name"></v-list-tile-title>
        </v-list-tile-content>
      </v-list-tile>

    </router-link>
  </v-list>

</template>

<script>
export default {
  name: "account_menu",
  data: () => {
    return {
      selected: -1
    };
  },
  mounted() {
    this.links.account.map((item, index) => {
      if (item.path === this.$route.path) this.select(index);
    });
  },
  watch: {
    $route: function(route) {
      this.links.account.map((item, index) => {
        if (item.path === route.path) this.select(index);
      });
    }
  },
  methods: {
    select(i) {
      if (this.selected !== -1) {
        document.getElementsByClassName("account_options")[this.selected].className =
          "account_options account_notchoosen ash--text";
      }
      document.getElementsByClassName("account_options")[i].className =
        "account_options";
      this.selected = i;
    }
  }
};
</script>

<style>
.account_notchoosen:hover {
  color: #daf1e7 !important;
}
#account_menu {
  padding-top: 5%;
}
</style>





