<template>
  <v-card flat class="ash_l">
    <v-card-title class="pb-1">
      <p class="title">{{sensor.transducer}} - {{sensor.measure}}</p>
    </v-card-title>
    <v-card-text class="py-0">
      <table style="width:100%">
        <tr>
          <td class="text-xs-right"><label class="primary_d--text mr-2">last commit</label></td>
          <th class="text-xs-left"><label v-if="sensor.last_commit">{{getTime(sensor.last_commit)}}</label></th>
          <td class="text-xs-right d-inline-flex"><v-icon v-if="(new Date() - new Date(sensor.last_commit))>300000">warning</v-icon></td>
        </tr>
        <tr>
          <td class="text-xs-right"><label class="primary_d--text mr-2">id</label></td>
          <th class="text-xs-left" colspan="2">{{sensor.id}}</th>
        </tr>
      </table>
      <div v-if="records" style="height: 250px;position:relative;">
        <canvas class="sensor_graph" :id="sensor.id"></canvas>
      </div>
      <v-layout row wrap>
        <v-flex class="py-0">
          <v-btn v-if="records.length>24" block color="primary" flat @click.native="getValues(1)"><v-icon>keyboard_arrow_left</v-icon></v-btn>
          <v-btn v-else block flat disabled><v-icon>keyboard_arrow_left</v-icon></v-btn>
        </v-flex>
        <v-flex class="py-0">
          <v-btn v-if="page>1" color="primary" block flat @click.native="getValues(-1)"><v-icon>keyboard_arrow_right</v-icon></v-btn>
          <v-btn v-else block flat disabled><v-icon>keyboard_arrow_right</v-icon></v-btn>
        </v-flex>
      </v-layout>
    </v-card-text>
  </v-card>
</template>

<script>
import Chart from "chart.js";
import { event_bus } from "@/plugins/bus.js";

export default {
  name: "sensor_graph",
  props: {
    sensor: Object,
    board: Object
  },
  data: () => {
    return {
      chart: null,
      records: [],
      page: 1
    };
  },
  mounted() {
    this.initGraph();
    this.getValues(0);
  },
  methods: {
    getValues(page) {
      event_bus.$emit("waiting", 20);
      event_bus.$data.http
        .get(
          "/record/board/" +
            this.board.id +
            "/sensor/" +
            this.sensor.id +
            "/page/" +
            (this.page + page)
        )
        .then(response => {
          this.records = response.data.records.sort(this.compare);
          this.page += page;
          this.designGraph();
          event_bus.$emit("waiting", 80);
        })
        .catch(error => {
          if (error.response) {
            event_bus.$emit("error", error.response.data);
          } else {
            event_bus.$emit("error", error.message);
          }
          event_bus.$emit("waiting", 80);
        });
    },
    initGraph() {
      this.chart = new Chart(document.getElementById(this.sensor.id), {
        type: "line",
        options: {
          legend: { display: false },
          scales: { xAxes: [{ display: false }] },
          responsive: true,
          maintainAspectRatio: false
        }
      });
    },
    designGraph() {
      console.log("records: " + this.records);
      let length = this.records.length;
      this.chart.data.labels = this.records.map(x => {
        return this.formatDate(x.datetime);
      });
      this.chart.data.datasets = [
        {
          label: this.sensor.measure,
          data: this.records.map(x => {
            return x.value;
          }),
          pointBackgroundColor: this.records.map(x => {
            return x.analyzed
              ? "rgba(152, 244, 70, 1)"
              : "rgba(255, 143, 40, 1)";
          }),
          backgroundColor: this.records.map(x => {
            return x.analyzed
              ? "rgba(152, 244, 70, 0.6)"
              : "rgba(255, 143, 40, 0.6)";
          }),
          borderWidth: 2
        },
        {
          label: "minimum acceptable",
          data: Array.from({ length }, i => this.sensor.min_acceptable),
          borderWidth: 2,
          fill: false,
          borderColor: "rgba(58, 100, 252, 1)",
          pointRadius: 0
        },
        {
          label: "maximum acceptable",
          data: Array.from({ length }, i => this.sensor.max_acceptable),
          borderWidth: 2,
          fill: false,
          borderColor: "rgba(255, 83, 35, 1)",
          pointRadius: 0
        }
      ];
      this.chart.update();
    },
    formatDate(date) {
      let monthNames = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec"
      ];
      let d = new Date(date);
      return (
        d.getHours() +
        ":" +
        d.getMinutes() +
        ":" +
        d.getSeconds() +
        " - " +
        d.getDate() +
        " " +
        monthNames[d.getMonth()] +
        " " +
        d.getFullYear()
      );
    },
    getTime(date) {
      let miliseconds = new Date() - new Date(date);
      if (miliseconds < 1000) return "<1 sec";
      else if (miliseconds < 60000) {
        return "~" + Math.floor(miliseconds / 1000) + " sec";
      } else if (miliseconds < 3600000) {
        return "~" + Math.floor(miliseconds / 60000) + " min";
      } else if (miliseconds < 86400000) {
        return Math.floor(miliseconds / 3600000) + " hours";
      } else return ">24 hours";
    },
    compare(a, b) {
      if (a.datetime < b.datetime) return -1;
      if (a.datetime > b.datetime) return 1;
      return 0;
    }
  }
};
</script>



