import Navbar from './Navbar.vue'

const links = {
  office: [
    {
      name: 'Dashboard',
      icon: 'dashboard',
      path: '/'
    },
    {
      name: 'Vitaboxes',
      icon: 'router',
      path: '/vitabox'
    },
    {
      name: 'Boards',
      icon: 'settings_remote',
      path: '/board'
    },
    {
      name: 'Users',
      icon: 'group',
      path: '/register'
    },
    {
      name: 'Utils',
      icon: 'help_outline',
      path: '/utils'
    }
  ],
  account: [
    {
      name: 'Change Password',
      icon: 'vpn_key',
      path: '/chgpwd'
    }
  ]
};

const NavbarPlugin = {

  install(Vue) {
    Vue.mixin({
      data() {
        return {
          links: links
        }
      }
    })

    Object.defineProperty(Vue.prototype, '$links', {
      get() {
        return this.$root.links
      }
    })
    Vue.component('navbar', Navbar)
  }
};

export default NavbarPlugin;



