<template>
  <div id="navbar">

    <v-navigation-drawer app v-model="drawer" id="side_bar" v-resize.quiet="resize" mobile-break-point=960>

      <v-bottom-nav :value="true" :active.sync="option" class="raven" id="top_menu">
        <v-btn flat color="primary" value="office">
          <span>Office</span>
          <v-icon>settings_applications</v-icon>
        </v-btn>
        <v-btn flat color="primary" value="account">
          <span>Account</span>
          <v-icon>account_box</v-icon>
        </v-btn>
      </v-bottom-nav>
      
      <div id="body_menu" class="raven">
        <account-menu v-if="option==='account'"></account-menu>
        <office-menu v-if="option==='office'"></office-menu>
      </div>

      <v-btn id="btn_logout" class="raven primary--text" @click="logout" block>
        <v-layout row class="pa-0">
          <v-flex xs4><v-icon class="pa-0">exit_to_app</v-icon></v-flex>
          <v-flex xs8 class="pa-0 text-xs-left">Logout</v-flex>
        </v-layout>
      </v-btn>

    </v-navigation-drawer>

    <v-toolbar class="primary--text" dark app id="top_bar">
      <v-toolbar-side-icon @click.stop="drawer = !drawer" class="hidden-md-and-up"></v-toolbar-side-icon>
      <v-toolbar-title>Backoffice</v-toolbar-title>
    </v-toolbar>
    
  </div>
</template>

<script>
import AccountMenu from "./Account";
import OfficeMenu from "./Office";
import { event_bus } from "@/plugins/bus.js";

export default {
  name: "navbar",
  data: () => {
    return {
      drawer: null,
      option: "office"
    };
  },
  methods: {
    logout() {
      event_bus.$data.token = null;
      this.$emit("logout");
    },
    resize() {
      if (
        screen.width > 960 &&
        this.drawer.className.indexOf("-temporary") !== -1
      ) {
        this.drawer.className =
          "navigation-drawer navigation-drawer--open navigation-drawer--floating";
      }
      if (
        screen.width < 960 &&
        this.drawer.className.indexOf("-temporary") === -1
      ) {
        this.drawer.className =
          "navigation-drawer navigation-drawer--close navigation-drawer--floating navigation-drawer--temporary";
      }
    }
  },
  components: {
    "account-menu": AccountMenu,
    "office-menu": OfficeMenu
  }
};
</script>

<style>
#side_bar {
  padding: 0;
}
#top_bar {
  height: 56px;
}
#top_menu {
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0 rgba(0, 0, 0, 0.14),
    0 1px 10px 0 rgba(0, 0, 0, 0.12);
}
#body_menu {
  height: calc(100% - 56px - 36px);
  position: relative;
}
#btn_logout {
  height: 36px;
  padding: 0;
  margin: 0;
  border-radius: 0;
}
#btn_logout > div > i {
  margin-right: 5px;
}
</style>


