<template>
  <div id="sensor_menu">
    <v-card>
      <v-card-title>
        <create-sensor @new="(val)=>sensors.push(val)"></create-sensor>

        <v-spacer></v-spacer>
        <v-text-field append-icon="search" label="Search" single-line hide-details v-model="search"></v-text-field>
      </v-card-title>
        
      <v-data-table :headers="headers" :search="search" :items="sensors" class="elevation-1" >
        <template slot="items" slot-scope="props">
          <td>{{ props.item.transducer }}</td>
          <td>{{ props.item.measure }}</td>
          <td>[{{ Math.round(props.item.min_acceptable) }};{{ Math.round(props.item.max_acceptable) }}]</td>
          <td>[{{ Math.round(props.item.min_possible) }};{{ Math.round(props.item.max_possible) }}]</td>
          <td class="right px-0">
            <v-btn icon class="mx-0" @click="editItem(props.item)"><v-icon color="secondary">edit</v-icon></v-btn>
            <v-btn icon class="mx-0" @click="deleteItention(props.item)"><v-icon color="error">delete</v-icon></v-btn>
          </td>
        </template>
      </v-data-table>
    </v-card>

    <v-dialog v-model="dialog_edit_sensor" max-width="500px">
      <edit-sensor @close="()=>dialog_edit_sensor=false" @update="(item)=>update(item)" :item="temp_sensor"></edit-sensor>
    </v-dialog>

    <v-dialog v-model="dialog_remove_sensor" max-width="500px">
      <delete-sensor @close="()=>dialog_remove_sensor=false" @delete="remove" :item="temp_sensor"></delete-sensor>
    </v-dialog>

  </div>
</template>

<script>
import { event_bus } from "@/plugins/bus.js";
import CreateSensor from "./CreateSensor";
import EditSensor from "./EditSensor";
import DeleteSensor from "./DeleteSensor";

export default {
  name: "sensor_menu",
  data: () => {
    return {
      search: "",
      headers: [
        { text: "Name", align: "left", value: "transducer" },
        { text: "Measure unit", align: "left", value: "measure" },
        { text: "Acceptable", align: "left", sortable: false },
        { text: "Possible", align: "left", sortable: false },
        { text: "Actions", align: "right", value: "id", sortable: false }
      ],
      sensors: [],
      temp_sensor: null,
      dialog_remove_sensor: false,
      dialog_edit_sensor: false
    };
  },
  created() {
    event_bus.$emit("waiting", 20);
    event_bus.$data.http
      .get("/sensor")
      .then(response => {
        this.sensors = response.data.sensors;
        event_bus.$emit("setSensors", response.data.sensors);
        event_bus.$emit("waiting", 30);
      })
      .catch(error => {
        if (error.response) {
          event_bus.$emit("toast", {
            message: error.response.data,
            type: "error"
          });
        } else {
          event_bus.$emit("toast", {
            message: error.message,
            type: "error"
          });
        }
        event_bus.$emit("waiting", 30);
      });
  },
  methods: {
    editItem(item) {
      this.dialog_edit_sensor = true;
      this.temp_sensor = item;
    },
    update(item) {
      this.sensors[this.sensors.indexOf(this.temp_sensor)] = item;
      this.dialog_edit_sensor = false;
    },
    deleteItention(item) {
      this.temp_sensor = item;
      this.dialog_remove_sensor = true;
    },
    remove() {
      this.sensors.splice(this.sensors.indexOf(this.temp_sensor), 1);
      this.dialog_remove_sensor = false;
    }
  },
  components: {
    "create-sensor": CreateSensor,
    "edit-sensor": EditSensor,
    "delete-sensor": DeleteSensor
  }
};
</script>

<style>

</style>


