<template>
  <div>
    <v-tabs icons-and-text centered grow slider-color="primary_d" color="primary">
      <v-tab class="primary_s--text" href="#tab-1">List<v-icon>list</v-icon></v-tab>
      <v-tab class="primary_s--text" href="#tab-2">Register<v-icon>edit</v-icon></v-tab>

      <v-tab-item :id="'tab-1'"><list-vitabox></list-vitabox></v-tab-item>
      <v-tab-item :id="'tab-2'"><register-vitabox></register-vitabox></v-tab-item>
    </v-tabs>
  </div>
</template>

<script>
import Register from "./Register";
import List from "./List";

export default {
  name: "vitabox_board",
  components: {
    "register-vitabox": Register,
    "list-vitabox": List
  }
};
</script>

<style>
@media only screen and (min-width: 960px) {
  .vitabox_column {
    padding: 7px;
  }
}
@media only screen and (max-width: 960px) {
  .vitabox_column {
    padding: 7px;
    padding-left: 7px;
    padding-right: 7px;
  }
}
</style>


