<template>
  <v-card tile id="vitabox_users">
    
    <v-card-title>
      <span class="headline">Patients</span>
      <v-spacer></v-spacer>
      <v-btn icon @click.native="close">
        <v-icon color="error">close</v-icon>
      </v-btn>
    </v-card-title>

    <v-card-actions class="py-0">
      <add-patient-to-box :box="item" @update="(patient)=>setPatient(patient)"></add-patient-to-box>
    </v-card-actions>

    <v-card-text>
      <v-divider light></v-divider>

      <v-container grid-list-md v-if="patients.length>0">
        <v-layout row wrap>
          <v-flex xs12 md6 child-flex v-for="patient in patients" :key="patient.id">
            <v-expansion-panel popout class="px-4">
              <v-expansion-panel-content>

                <div slot="header">
                  <v-list-tile>
                    <v-list-tile-avatar>
                      <img src="https://www.elastic.co/assets/bltada7771f270d08f6/enhanced-buzz-1492-1379411828-15.jpg">
                    </v-list-tile-avatar>
                    <v-list-tile-content>
                      <v-list-tile-title v-html="patient.name"></v-list-tile-title>
                    </v-list-tile-content>
                  </v-list-tile>
                </div>

                <div>
                  <table>
                    <tr>
                      <th class="text-xs-right"><label class="subheading primary_d--text mx-2">age</label></th>
                      <th><h3>{{patient.age}}</h3></th>
                    </tr>
                    <tr>
                      <th class="text-xs-right"><label class="subheading primary_d--text mx-2">gender</label></th>
                      <th><h3>{{patient.gender}}</h3></th>
                    </tr>
                  </table>
                  <v-divider></v-divider>

                  <v-container grid-list-md>
                    <v-layout row wrap>
                      <v-flex xs8 child-flex>
                        <v-tooltip bottom>
                          <v-btn block small dark class="my-0" color="indigo" slot="activator" @click.native="()=>{}"><v-icon>dashboard</v-icon></v-btn>
                          <span>dashboard</span>
                        </v-tooltip>
                      </v-flex>
                      <v-flex xs4 child-flex>
                        <v-tooltip bottom>
                          <v-btn block small class="my-0" color="error" slot="activator" @click.native="removeIntention(patient)"><v-icon>remove_circle</v-icon></v-btn>
                          <span>remove</span>
                        </v-tooltip>
                      </v-flex>
                    </v-layout>
                  </v-container>

                </div>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-flex>
        </v-layout>
      </v-container>

      
      <div v-else class="subheading text-xs-center warning--text">This vitabox has no patients</div>
    </v-card-text>

    <v-dialog v-model="dialog_remove_patient" max-width="500px">
      <remove-patient-from-vitabox @close="()=>dialog_remove_patient=false" @remove="remove" :patient="temp_patient" :box="item"></remove-patient-from-vitabox>
    </v-dialog>

  </v-card>
</template>

<script>
import { event_bus } from "@/plugins/bus.js";
import AddPatient from "./AddPatient.vue";
import RemovePatient from "./RemovePatient.vue";

export default {
  name: "vitabox_users",
  props: {
    item: Object
  },
  data: () => {
    return {
      patients: [],
      dialog_remove_patient: false,
      temp_patient: null
    };
  },
  created() {
    event_bus.$emit("waiting", 20);
    event_bus.$data.http
      .get("/vitabox/" + this.item.id + "/patient")
      .then(response => {
        event_bus.$emit("waiting", 40);
        let today = new Date();
        response.data.patients.map(x => {
          let birthdate = new Date(x.birthdate);
          this.patients.push({
            birthdate: birthdate,
            age:
              today.getMonth() < birthdate.getMonth() ||
              (today.getMonth() === birthdate.getMonth() &&
                today.getDate() < birthdate.getDate())
                ? today.getFullYear() - birthdate.getFullYear() - 1
                : today.getFullYear() - birthdate.getFullYear(),
            name: x.name,
            id: x.id,
            gender: x.gender,
            since: x.since
          });
        });
        event_bus.$emit("waiting", 40);
      })
      .catch(error => {
        if (error.response) {
          event_bus.$emit("toast", {
            message: error.response.data,
            type: "error"
          });
        } else {
          event_bus.$emit("toast", {
            message: error.message,
            type: "error"
          });
        }
        event_bus.$emit("waiting", 80);
      });
  },
  watch: {
    item(val) {
      event_bus.$emit("waiting", 20);
      event_bus.$data.http
        .get("/vitabox/" + val.id + "/patient")
        .then(response => {
          event_bus.$emit("waiting", 40);
          this.patients = [];
          let today = new Date();
          response.data.patients.map(x => {
            let birthdate = new Date(x.birthdate);
            this.patients.push({
              birthdate: birthdate,
              age:
                today.getMonth() < birthdate.getMonth() ||
                (today.getMonth() === birthdate.getMonth() &&
                  today.getDate() < birthdate.getDate())
                  ? today.getFullYear() - birthdate.getFullYear() - 1
                  : today.getFullYear() - birthdate.getFullYear(),
              name: x.name,
              id: x.id,
              gender: x.gender,
              since: x.since
            });
          });
          event_bus.$emit("waiting", 40);
        })
        .catch(error => {
          if (error.response) {
            event_bus.$emit("toast", {
              message: error.response.data,
              type: "error"
            });
          } else {
            event_bus.$emit("toast", {
              message: error.message,
              type: "error"
            });
          }
          event_bus.$emit("waiting", 80);
        });
    }
  },
  methods: {
    close() {
      this.$emit("close");
    },
    setPatient(item) {
      this.patients.push(item);
    },
    removeIntention(item) {
      this.dialog_remove_patient = true;
      this.temp_patient = item;
    },
    remove() {
      this.patients.splice(this.patients.indexOf(this.temp_patient), 1);
      this.dialog_remove_patient = false;
    }
  },
  components: {
    "add-patient-to-box": AddPatient,
    "remove-patient-from-vitabox": RemovePatient
  }
};
</script>




