import Vue from 'vue'
import Router from 'vue-router'
import Vitabox from '@/components/vitabox/Index'
import Board from '@/components/board/Index'
import Utils from '@/components/utils/utils'
import ChgPwd from '@/components/auth/ChgPwd'
import BoardDashboard from '@/components/dashboard/Board'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/vitabox',
      name: 'Vitabox',
      component: Vitabox
    },
    {
      path: '/board',
      name: 'Board',
      component: Board
    },
    {
      path: '/utils',
      name: 'Utils',
      component: Utils
    },
    {
      path: '/chgpwd',
      name: 'ChgPwd',
      component: ChgPwd
    },
    {
      path: '/dashboard/board',
      name: 'BoardDashboard',
      component: BoardDashboard,
      props: true
    }
  ]
})



