<template>
<v-dialog id="create_model" v-model="dialog_create_model" max-width="500px">
  <v-btn color="primary" large dark icon slot="activator" class="mb-2">
    <v-icon>add</v-icon>
  </v-btn>
  <v-card>
    <v-card-title>
      <span class="headline primary_d--text">Add board model</span>
      <v-spacer></v-spacer>
      <v-btn icon @click.native="()=>dialog_create_model=false">
        <v-icon color="error">close</v-icon>
      </v-btn>
    </v-card-title>
    <v-card-text>
      <v-container grid-list-md>
        <v-layout wrap>
          <v-flex xs12 sm6 md8>
            <v-text-field :rules="[() => model.name.length > 3 || 'Board name is required']" label="Board name" v-model="model.name"></v-text-field>
          </v-flex>
          <v-flex xs12 sm6 md4>
            <v-select :rules="[() => model.type.length > 1 || 'Board type is required']" :items="items" label="Board type" v-model="model.type" single-line></v-select>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn dark color="ash" @click.native="save">Save</v-btn>
    </v-card-actions>
  </v-card>
</v-dialog>
</template>

<script>
import { event_bus } from "@/plugins/bus.js";

export default {
  name: "create_model",
  data: () => {
    return {
      model: {
        id: "",
        name: "",
        type: ""
      },
      items: ["environmental", "wearable", "non-wearable"],
      dialog_create_model: false
    };
  },
  methods: {
    save() {
      if (this.model.name !== "" && this.model.type !== "") {
        event_bus.$emit("waiting", 50);
        event_bus.$data.http
          .post("/boardmodel", this.model)
          .then(response => {
            this.model.id = response.data.id;
            this.dialog_create_model = false;
            this.$emit("new", this.model);
            event_bus.$emit("toast", {
              message: "board model was successfully created",
              type: "success"
            });
            event_bus.$emit("waiting", 50);
          })
          .catch(error => {
            if (error.response) {
              event_bus.$emit("toast", {
                message: error.response.data,
                type: "error"
              });
            } else {
              event_bus.$emit("toast", {
                message: error.message,
                type: "error"
              });
            }
            event_bus.$emit("waiting", 50);
          });
      } else {
        event_bus.$emit("error", "insert all fields");
      }
    }
  }
};
</script>



