<template>
  <div id="progressbar">
      <v-progress-linear v-model="complete" :query="true" :active="waiting" class="my-0"></v-progress-linear>
  </div>
</template>

<script>
import { event_bus } from "@/plugins/bus.js";

export default {
  name: "progressbar",
  data: () => {
    return {
      waiting: false,
      complete: 0
    };
  },
  mounted() {
    event_bus.$on("waiting", val => {
      if (this.complete === 0) this.waiting = true;
      this.complete += val;
      if (this.complete >= 99) {
        this.waiting = false;
        this.complete = 0;
      }
    });
  }
};
</script>

<style>
#progressbar {
  position: absolute;
  top: 0;
  z-index: 1500;
  width: 100%;
}
</style>



