<template>
  <v-app id="app">
    
    <progress-bar></progress-bar>

    <div v-if="logged">
      <navbar @logout="logout"></navbar>

      <main>
        <router-view class="cinza_claro"></router-view>
      </main>
      
      <foot></foot>
    </div>

    <auth v-else></auth>
    
    <log></log>

  </v-app>
</template>

<script>
import { event_bus } from "@/plugins/bus.js";
import Auth from "@/components/auth/Index.vue";
import Footer from "@/components/utils/Footer.vue";
import Log from "@/components/utils/Log.vue";
import Progress from "./components/utils/Progress.vue";

export default {
  name: "app",
  data: () => {
    return {
      logged: !(
        localStorage.getItem("token") === undefined ||
        localStorage.getItem("token") === null
      )
    };
  },
  components: {
    auth: Auth,
    foot: Footer,
    log: Log,
    "progress-bar": Progress
  },
  mounted() {
    event_bus.$on("navigate", path => this.navigateTo(path));
    event_bus.$on("login", () => (this.logged = true));
  },
  methods: {
    logout() {
      this.logged = false;
    },
    navigateTo(path) {
      this.$router.go(path);
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media only screen and (min-width: 960px) {
  main {
    padding: calc(1% + 56px) 1% calc(1% + 32px) calc(1% + 300px);
    position: relative;
  }
}
@media only screen and (max-width: 960px) {
  main {
    padding: calc(1% + 56px) 1%;
    position: relative;
  }
}
a {
  text-decoration: none;
}
i {
  color: inherit !important;
}
.expansion-panel--inset .expansion-panel__container--active,
.expansion-panel--popout .expansion-panel__container--active {
  box-shadow: none !important;
}
.bottom-sheet {
  height: 100% !important;
}
.bottom-sheet .card {
  height: 100% !important;
  overflow-y: auto;
}
.input-group__details {
  min-height: 0 !important;
}
.btn {
  min-width: 0;
}
</style>



